@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap'); */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}


body {
  background-color: #fafafa;
}

.wrapper {
    width: 100%;
    height: 100vh;
    background-color: #fafafa;
}

.hero-author {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    background-color: #222;
}

.hero-author p {
    color: #CCD1D1;
    font-size: 17px;
    font-weight: 300;
}

.hero-author p a {
    text-decoration: none;
    color: #CCD1D1;
    transition: all 0.3s ease-in-out;
}

.hero-author p a:hover {
    color: #E74C3C;
}

/*  HERO SECTION IN HEADER */
.hero-section {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 10%;
    background-color: #E74C3C;
}

.hero-logo{
    color: #fff;
    padding: 30px;
    transition: all 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.hero-title {
    font-size: 50px;
    font-weight: 700;
    line-height: 50px;
}

.hero-location {
    font-size: 28px;
    font-weight: 300;
}

/* ADD SECTION */
.add-section {
    padding-right: 10%;
}
.add-section img {
    width: 60px;
    cursor: pointer;
}

@media(max-width: 600px) {
    /* HERO SECTION*/
    .hero-section {
        padding-left: 0;
    }

    /* HERO SECTION*/
    .hero-section {
        padding-left: 0;
    }

    .hero-logo{
        padding: 25px;
    }
    
    .hero-title {
        font-size: 38px;
    }
    
    .hero-location {
        font-size: 20px;
    }

    /* ADD SECTION */
    .add-section {
        padding-right: 4%;
    }
    .add-section img {
        width: 60px;
        cursor: pointer;
    }
}

@media(max-width: 360px){
    /* AUTHOR */
    .hero-author p {
        font-size: 14px;
    }

    /* HERO SECTION*/
    .hero-section {
        padding-left: 0;
    }
    .hero-logo{
        padding: 20px;
    }
    
    .hero-title {
        font-size: 30px;
    }
    
    .hero-location {
        font-size: 15px;
    }

    /* ADD SECTION */
    .add-section {
        padding-right: 4%;
    }
    .add-section img {
        width: 40px;
        cursor: pointer;
    }
}
.subject-wrapper {
    width: 80%;
    margin: auto;
    padding: 20px 0 20px 0;
}

.subject-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.subject-title {
    font-size: 35px;
    font-weight: 400;
    color: #3b3b3b;
    padding-top: 30px;
    padding-bottom: 5px;
}

.subject-card-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

@media(max-width: 600px) {
    .subject-wrapper {
        width: 95%;
        margin: auto;
        padding: 20px 0 20px 0;
    }

    .subject-title {
        font-size: 25px;
        padding-top: 20px;
    }
}

@media(max-width: 320px) {
    .subject-wrapper {
        width: 100%;
        margin: auto;
        padding: 20px 0 20px 0;
    }

    .subject-title {
        font-size: 22px;
        padding-top: 20px;
    }
}





.card-container {
    width: 200px;
    height: 250px;
    padding: 10px;
}

.card-body {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 2px 2px 8px 1px rgba(0, 0, 0, 0.12);
    cursor: pointer;
}

.card-body:active {
    box-shadow: 2px 2px 8px 1px rgba(0, 0, 0, 0);
}

.card-image {
    width: 130px;
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #3b3b3b;
    border-radius: 50%;
    border: solid 4px #E74C3C;
}

.card-image img {
    width: 90px;
}

.card-title {
    font-size: 16px;
    text-align: center;
    font-weight: 400;
    margin-top: 10px;
    color: #3b3b3b;
}

@media(max-width: 320px) {
    .card-image {
        width: 120px;
        height: 120px;
    }

    .card-image img {
        width: 80px;
    }

    .card-title {
        font-size: 14px;
    }

    .card-container:first-child {
        padding-right: 2.5px;
    }

    .card-container:last-child {
        padding-left: 3.5px;
    }

}

@media(max-width: 400px) {

    .card-container:first-child {
        padding-right: 2.5px;
    }

    .card-container:last-child {
        padding-left: 3.5px;
    }

}
.menuquestions--container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    background-color: #fafafa;
}

.menuquestions--wrapper {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.menuquestions--title {
    font-size: 35px;
    font-weight: 400;
    color: #3b3b3b;
    padding-top: 30px;
    padding-bottom: 5px;
    margin-bottom: 20px;
}

.menuquestions--container-items {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.menuquestions--container-items h2 {
    font-size: 25px;
    font-weight: 400;
    color: #3b3b3b;
}

.menuquestions--bank-questions {
    list-style: none;
    margin-bottom: 15px;
}

.menuquestions--bank-questions li {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border: solid 2px #2222;
    border-bottom: none;
    border-radius: 1px; /* i don't know*/
    cursor: pointer;
    color: #3b3b3b;
    font-size: 18px;
    font-weight: 400;
}

.menuquestions--bank-questions li:last-child {
    border-bottom: solid 2px #2222;
}

.menuquestions--bank-questions li button {
    width: 25px;
    height: 25px;
    background-image: url(/web-quiz-app/static/media/no-check.ca41c76d.svg);
    background-repeat: no-repeat;
    -webkit-filter: invert(0.4) sepia(1);
            filter: invert(0.4) sepia(1);
    transition: all 0.2s ease-in-out;
    border: none;
    background-color: transparent;
}

.menuquestions--bank-questions li button.active {
    background-image: url(/web-quiz-app/static/media/check.8b4d6e74.svg);
    background-repeat: no-repeat;
    -webkit-filter: none;
            filter: none;
}



@media(max-width: 800px) {
    .menuquestions--wrapper {
        width: 90%;
    }
    .menuquestions--title {
        font-size: 25px;
        padding-top: 20px;
    }
}

@media(max-width: 320px) {
    .menuquestions--wrapper {
        width: 95%;
    }
    .menuquestions--title {
        font-size: 22px;
        padding-top: 20px;
    }
}
.button-primary {
    height: 43px;
    background-color: #E74C3C;
    border: none;
    border-radius: 5px;
    margin: 10px;
    color: #fff;
    font-size: 20px;
    font-weight: 300;
    cursor: pointer;
}
.quiz--container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 30px;
    background-color: #fafafa;
}

.quiz--wrapper {
    width: 35%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    background-color: #fafafa;
}

.quiz--question {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    box-shadow: 1px 1px 10px 0px rgb(0, 0, 0, 0.1);
    border-radius: 10px;
    margin-bottom: 10px;
}

.quiz--question--title {
    font-size: 22px;
    font-weight: 400;
    color: #3b3b3b;
    text-align: justify;
    line-height: 25px;
    padding: 30px 35px 0px 35px;
}

.quiz--question ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.quiz--question ul li {
    width: 100%;
    padding: 0;
    list-style: none;
}

/* ANSWER SECTION */
.correct {
    background-color: #58D68D !important;
    color: #fff !important;
}

.wrong {
    background-color: #B03A2E !important;
    color: #fff !important;
}

/* RESULT SECTION */
.result--wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.result--wrapper h2 {
    font-size: 35px;
    font-weight: 400;
    color: #3b3b3b;
    text-align: justify;
    line-height: 25px;
    padding: 30px 35px 0px 35px;
    margin-bottom: 25px;
}

.result--wrapper p  {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 26px;
    color: #3b3b3b;
    font-weight: 300;
    border: solid 2px #bdbdbd;
    padding: 5px 0px 5px 20px;
    margin: 5px 0px 5px 0px;
    border-radius: 10px;
}

.result--wrapper p img {
    width: 30px;
    margin-right: 18px;
}

.result--wrapper p span {
    width: 30px;
    margin-right: 18px;
    text-align: center;
}

.result--wrapper button:nth-child(5) {
    margin: 30px 0px 5px 0px;
}

.result--wrapper button:nth-child(6) {
    margin: 5px 0px 20px 0px;
}

@media(max-width: 1370px) {
    .quiz--wrapper {
        width: 50%;
        display: flex;
    }
}

@media(max-width: 800px) {
    .quiz--wrapper {
        width: 90%;
    }
    .quiz--question--title {
        font-size: 20px;
    }
}

@media(max-width: 320px) {
    .quiz--wrapper {
        width: 95%;
    }
    .quiz--question--title {
        font-size: 22px;
    }
}
.button-void {
    background-color: #fff;
    border: solid 2px #a0a0a0;
    border-radius: 10px;
    margin: 10px 0px 0px 0px;
    padding: 5px 5px 5px 20px;
    color: #222222;
    font-size: 20px;
    font-weight: 400;
    cursor: pointer;
    text-align: left;
    transition: all 0.2s ease-in-out;
    box-shadow: 1px 2px 4px 0px rgb(0, 0, 0, 0.2);
}

.button-void:hover {
    box-shadow: none;
}

.button-void:focus {
    outline: 0;
}
.bar-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0px 10px 0px;
    color: #3b3b3b;
}

.bar-questions {
    width: 50%;
    display: flex;
    flex-direction: row;
}

.bar-questions h2 {
    font-size: 35px;
}

.bar-questions p {
    display: inline-block;
    font-size: 18px;
    font-weight: 300;
    margin-left: 2px;
}

.bar-summary {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.summary-correct, .summary-wrong {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.summary-correct img, .summary-wrong img {
    width: 25px;
    margin-left: 25px;
}

.summary-correct p, .summary-wrong p {
    font-size: 18px;
    margin-left: 5px;
}
